'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import Image from 'next/image'
import { X, ExternalLink } from 'lucide-react'

interface AdPlacement {
  id: string
  title: string
  image?: string
  link?: string
  description?: string
  type: 'banner' | 'sidebar' | 'featured' | 'sponsored'
  isSponsored?: boolean
}

interface AdServingProps {
  placementCode: string
  className?: string
  maxWidth?: number
  maxHeight?: number
  showCloseButton?: boolean
}

export default function AdServing({ 
  placementCode, 
  className = '', 
  maxWidth, 
  maxHeight, 
  showCloseButton = false 
}: AdServingProps) {
  const [ad, setAd] = useState<AdPlacement | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [isClosed, setIsClosed] = useState(false)
  const [impressionRecorded, setImpressionRecorded] = useState(false)

  // Helper function to get CSS classes based on ad type and dimensions
  const getAdSizeClasses = (type: string) => {
    switch (type) {
      case 'banner':
        return 'w-full max-w-[970px] h-[250px]'
      case 'sidebar':
        return 'w-full max-w-[300px] h-[250px]'
      case 'featured':
        return 'w-full max-w-[400px] h-[300px]'
      default:
        return 'w-full max-w-[300px] h-[250px]'
    }
  }

  useEffect(() => {
    // Mock ad data - in real app, fetch from ad serving API
    const mockAds: Record<string, AdPlacement> = {
      'homepage_leaderboard': {
        id: '1',
        title: 'Premium Real Estate - Kigali Heights',
        image: '/api/placeholder?width=970&height=250',
        link: '/listings/houses',
        description: 'Luxury apartments in the heart of Kigali',
        type: 'banner',
        isSponsored: true
      },
      'homepage_sidebar': {
        id: '2',
        title: 'Car Insurance - Rwanda Insurance',
        image: '/api/placeholder?width=300&height=600',
        link: '#',
        description: 'Protect your vehicle with comprehensive coverage',
        type: 'sidebar',
        isSponsored: true
      },
      'category_top': {
        id: '3',
        title: 'Property Management Services',
        image: '/api/placeholder?width=728&height=90',
        link: '#',
        description: 'Professional property management for landlords',
        type: 'banner',
        isSponsored: true
      },
      'featured_carousel': {
        id: '4',
        title: 'Featured: Modern Villa in Nyarutarama',
        image: '/api/placeholder?width=400&height=300',
        link: '/listings/houses/1',
        description: '5 bedrooms, swimming pool, garden',
        type: 'featured',
        isSponsored: false
      },
      'listing_sidebar': {
        id: '5',
        title: 'Home Loan Services - Bank of Kigali',
        image: '/api/placeholder?width=300&height=250',
        link: '#',
        description: 'Get pre-approved for your dream home',
        type: 'sidebar',
        isSponsored: true
      }
    }

    // Simulate API call delay
    setTimeout(() => {
      const selectedAd = mockAds[placementCode]
      if (selectedAd) {
        setAd(selectedAd)
      }
      setIsLoading(false)
    }, 500)
  }, [placementCode])

  useEffect(() => {
    // Record impression when ad is visible and not already recorded
    if (ad && !impressionRecorded && !isClosed) {
      // In real app, call analytics API
      console.log(`Recording impression for ad ${ad.id} at placement ${placementCode}`)
      setImpressionRecorded(true)
    }
  }, [ad, impressionRecorded, isClosed, placementCode])

  const handleClick = () => {
    if (ad) {
      // Record click in analytics
      console.log(`Recording click for ad ${ad.id}`)
      
      // Open link in new tab if external
      if (ad.link?.startsWith('#')) {
        // Internal link - navigate within app
        window.location.href = ad.link
      } else if (ad.link) {
        // External link
        window.open(ad.link, '_blank', 'noopener,noreferrer')
      }
    }
  }

  const handleClose = () => {
    setIsClosed(true)
  }

  // Don't render anything if no ad or closed
  if (!ad || isClosed || isLoading) {
    if (isLoading) {
      return (
        <div className={`${className} bg-gray-100 animate-pulse flex items-center justify-center ${getAdSizeClasses(ad?.type || 'banner')}`}> 
          <div className="text-gray-400 text-sm">Loading ad...</div>
        </div>
      )
    }
    return null
  }

  // Different rendering based on ad type
  const renderAd = () => {
    const baseClasses = `relative group cursor-pointer ${className} ${getAdSizeClasses(ad.type)}`

    switch (ad.type) {
      case 'banner':
        return (
          <div className={baseClasses} onClick={handleClick}>
            <div className="relative overflow-hidden rounded-lg">
              {ad.image ? (
                <Image
                  src={ad.image}
                  alt={ad.title}
                  width={maxWidth || 728}
                  height={maxHeight || 250}
                  className="w-full h-auto object-cover"
                  unoptimized={ad.image?.startsWith('/api/placeholder')}
                />
              ) : (
                <div className={`bg-gray-200 flex items-center justify-center ${getAdSizeClasses('banner')}`}> 
                  <span className="text-gray-500">Ad Space</span>
                </div>
              )}
              {ad.isSponsored && (
                <div className="absolute top-2 left-2 bg-black/70 text-white text-xs px-2 py-1 rounded">
                  SPONSORED
                </div>
              )}
              {ad.description && (
                <div className="absolute bottom-0 left-0 right-0 bg-black/70 text-white p-2 text-sm">
                  {ad.description}
                </div>
              )}
            </div>
          </div>
        )

      case 'sidebar':
        return (
          <div className={baseClasses} onClick={handleClick}>
            {showCloseButton && (
              <button
                onClick={handleClose}
                className="absolute top-2 right-2 z-10 bg-white/80 hover:bg-white rounded-full p-1 opacity-0 group-hover:opacity-100 transition-opacity"
              >
                <X className="h-3 w-3 text-gray-600" />
              </button>
            )}
            <div className="bg-white rounded-lg shadow-sm border overflow-hidden">
              {ad.image ? (
                <Image
                  src={ad.image}
                  alt={ad.title}
                  width={maxWidth || 300}
                  height={maxHeight || 250}
                  className="w-full h-auto object-cover"
                  unoptimized={ad.image?.startsWith('/api/placeholder')}
                />
              ) : (
                <div className={`bg-gray-100 flex items-center justify-center p-8 ${getAdSizeClasses('sidebar')}`}> 
                  <span className="text-gray-500 text-center">Ad Space<br/>{maxWidth}x{maxHeight}</span>
                </div>
              )}
              <div className="p-3">
                <h4 className="font-semibold text-sm text-gray-900 mb-1">{ad.title}</h4>
                {ad.description && (
                  <p className="text-xs text-gray-600 mb-2">{ad.description}</p>
                )}
                <div className="flex items-center justify-between">
                  {ad.isSponsored && (
                    <span className="text-xs text-gray-500">SPONSORED</span>
                  )}
                  <ExternalLink className="h-3 w-3 text-gray-400" />
                </div>
              </div>
            </div>
                      </div>
        )

      case 'featured':
        return (
          <div className={baseClasses} onClick={handleClick}>
            <div className="bg-white rounded-lg shadow-sm border overflow-hidden hover:shadow-md transition-shadow">
              {ad.image ? (
                <Image
                  src={ad.image}
                  alt={ad.title}
                  width={maxWidth || 400}
                  height={maxHeight || 300}
                  className="w-full h-auto object-cover"
                  unoptimized={ad.image?.startsWith('/api/placeholder')}
                />
              ) : (
                <div className={`bg-gray-100 flex items-center justify-center ${getAdSizeClasses('featured')}`}> 
                  <span className="text-gray-500">Featured Listing</span>
                </div>
              )}
              <div className="p-3">
                <h4 className="font-semibold text-gray-900 mb-1">{ad.title}</h4>
                {ad.description && (
                  <p className="text-sm text-gray-600">{ad.description}</p>
                )}
                {ad.isSponsored && (
                  <div className="mt-2">
                    <span className="bg-red-100 text-red-600 text-xs px-2 py-1 rounded">
                      PROMOTED
                    </span>
                  </div>
                )}
              </div>
            </div>
                      </div>
        )

      default:
        return (
          <div className={baseClasses} onClick={handleClick}>
            <div className="bg-gray-100 border-2 border-dashed border-gray-300 rounded-lg flex items-center justify-center p-4">
              <div className="text-center">
                <div className="text-gray-500 mb-2">Ad Space</div>
                <div className="text-xs text-gray-400">{maxWidth}x{maxHeight}</div>
              </div>
            </div>
          </div>
        )
    }
  }

  return renderAd()
}

// Predefined placement components for easy use
export function HomepageLeaderboard(props: Omit<AdServingProps, 'placementCode'>) {
  return <AdServing {...props} placementCode="homepage_leaderboard" maxWidth={970} maxHeight={250} />
}

export function HomepageSidebar(props: Omit<AdServingProps, 'placementCode'>) {
  return <AdServing {...props} placementCode="homepage_sidebar" maxWidth={300} maxHeight={600} />
}

export function CategoryTopBanner(props: Omit<AdServingProps, 'placementCode'>) {
  return <AdServing {...props} placementCode="category_top" maxWidth={728} maxHeight={90} />
}

export function ListingSidebar(props: Omit<AdServingProps, 'placementCode'>) {
  return <AdServing {...props} placementCode="listing_sidebar" maxWidth={300} maxHeight={250} />
}

export function FeaturedCarousel(props: Omit<AdServingProps, 'placementCode'>) {
  return <AdServing {...props} placementCode="featured_carousel" maxWidth={400} maxHeight={300} />
}
